import { Box, Button, Link, Typography, styled } from '@mui/material';

import CloudUploadSharpIcon from '@mui/icons-material/CloudUploadSharp';

import HeaderSearch from './Search';
import HeaderNotifications from './Notifications';
import { useRef, useState } from 'react';
import { createResource } from 'src/config/SimpleApis';
import { getToken } from 'src/config/helper';
import { post_photos } from 'src/config/WebServices';

function HeaderButtons() {

  const inputRef = useRef(null);
  const [selectedImage, setSelectedImage] = useState(null);

  const handleClick = () => {
    // 👇️ open file input box on click of other element
    inputRef.current.click();
  };

  const handleFileChange = async event => {
    const fileObj = event.target.files && event.target.files[0];
    if (!fileObj) {
      return;
    }

    console.log('fileObj is', fileObj);

    // 👇️ reset file input
    event.target.value = null;

    // 👇️ is now empty
    console.log(event.target.files);

    // 👇️ can still access file object here
    console.log(fileObj);
    console.log(fileObj.name);

    const formData = new FormData();
    formData.append("photo", fileObj);

    try {
      const token = getToken();
      const res = await createResource(post_photos,formData, token);
      console.log("Photoupload:",res)
      alert("Photo Uploaded..")
    } catch (e) {
      console.log("Photoupload:",e)
    } finally {
    }
  };

  return (
    <Box sx={{ mr: 1 }}>
      {/* <HeaderSearch /> */}
      <Box sx={{ mx: 0.5 }} component="span">
        {/* <HeaderNotifications /> */}
        <Button
          onClick={handleClick}
          // rel="noopener noreferrer"
          sx={{ mt: { xs: 2, md: 0 }, background: '#4D9900' }}
          variant="contained"
          startIcon={<CloudUploadSharpIcon fontSize="small" />}
        >
          Uploads
        </Button>
        <input type="file" id="file"
          ref={inputRef}
          onChange={handleFileChange} style={{ display: 'none' }}></input>
      </Box>
    </Box>
  );
}

export default HeaderButtons;
